<template>
    <p-toast />
    <div class="p-grid">
        <div class="p-col-12 p-md-6 p-lg-4 p-md-offset-3 p-lg-offset-4 p-mt-5">
            <p-card>
                <template #title> Update Password </template>
                <template #content>
                    <div class="p-fluid">
                        <div class="p-field">
                            <label for="lastname">User</label>
                            <p-dropdown
                                v-model="form.username"
                                :options="users"
                                optionValue="username"
                                optionLabel="name"
                            />
                        </div>
                        <div
                            v-if="errorMsg._401 !== null && submitted"
                            class="p-field p-error"
                        >
                            {{ errorMsg._401 }}
                        </div>

                        <div class="p-field">
                            <label for="lastname">New Password</label>
                            <p-password
                                v-model="form.newPassword"
                                toggleMask
                                :feedback="false"
                                class="p-inputtext-lg"
                                :class="{ 'p-invalid': errorMsg._400 !== null }"
                            />
                        </div>

                        <div class="p-field">
                            <label for="lastname">Confirm Password</label>
                            <p-password
                                v-model="form.confirmPassword"
                                toggleMask
                                :feedback="false"
                                class="p-inputtext-lg"
                                :class="{ 'p-invalid': errorMsg._400 !== null }"
                            />
                        </div>
                        <div
                            v-if="errorMsg._400 !== null && submitted"
                            class="p-field p-error"
                        >
                            {{ errorMsg._400 }}
                        </div>
                    </div>
                </template>
                <template #footer>
                    <p-button
                        label="Submit"
                        :loading="isLoading"
                        @click="submit"
                        class="p-mr-2"
                    />

                    <p-button label="Back" @click="back" />
                </template>
            </p-card>
        </div>
    </div>
</template>

<script>
import { computed, onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";
import { useRoute, useRouter } from "vue-router";

export default {
    setup() {
        const store = useStore();
        const toast = useToast();
        const route = useRoute();
        const router = useRouter();

        const form = reactive({
            username: null,
            newPassword: null,
            confirmPassword: null
        });

        const isLoading = ref(false);
        const errorMsg = reactive({
            _401: null,
            _404: null,
            _400: null
        });

        const users = computed(() => store.state.users.users);

        const submitted = ref(false);
        const submit = () => {
            isLoading.value = true;
            submitted.value = true;
            store
                .dispatch("users/updatePasswordAdmin", {
                    ...form
                })
                .then(() => {
                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Password Change Successfully",
                        life: 3000
                    });
                    form.username = null;
                    form.newPassword = null;
                    form.confirmPassword = null;
                })
                .catch((err) => {
                    if (err.response.status == 404) {
                        errorMsg._404 = err.response.data.message;
                        errorMsg._401 = null;
                    } else if (err.response.status == 401) {
                        errorMsg._401 = err.response.data.message;
                    } else if (err.response.status == 400) {
                        errorMsg._400 = err.response.data.message;
                        errorMsg._401 = null;
                    }
                })
                .finally(() => {
                    isLoading.value = false;
                });
        };

        onMounted(() => {
            if (route.query.username) {
                form.username = route.query.username;
            }

            store.dispatch("users/getUsers");
        });

        return {
            form,
            submit,
            users,
            submitted,
            isLoading,
            errorMsg,
            back: () => {
                router.go(-1);
            }
        };
    }
};
</script>
